html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.root-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.sub-container {
  height: 100%;
  display: flex;
  background-color: hwb(0 85% 14%); /*tek da ne bude belo*/
}

.left-div {
  flex: 1;
  height: 100%;
  background-color: #999;
  flex-direction: column;
}

.right-div {
  flex: 2;
  height: 100%;
  background-color: #333;
  flex-direction: column;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50vh;
  max-width: 50vh;
  margin: 50px auto;
}

.row {
  display: flex;
  flex: 1;
  width: 100%;
}

.cell {
  flex: 1;
  border: 1px solid lightslategrey;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;
  color: yellow;
}
